.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.levels {
  display: flex;
  flex-flow: column;
  // align-items: center;
}

.level {
  border: 1px solid black;
  display: flex;
  flex-flow: column;

  // width: 600px;
  // box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  // margin-bottom: 20px;
}

.level-empty {
  opacity: 0.5;
}

.green {
  color: rgb(0, 128, 32);
}

.yellow {
  color: #b5d400;
}

.inputs {
  input {
    height: 40px;
    border: 1px solid #ccc;
    border-radius: 5px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    padding-left: 10px;
  }

  button {
    color: white;
    background: linear-gradient(270deg, #00a8f5 -125.02%, #006fe8 98.68%);
    border: 1px solid #ccc;
    border-radius: 5px;
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    height: 44px;
    padding: 0 15px;
  }
}